'use client';

import { useState, useCallback } from 'react';
import dynamic from 'next/dynamic';
import Navbar from '@/components/layout/NavbarSection';
import Footer from '@/components/layout/FooterSection';
import Hero from '@/components/sections/Hero';
import LoadingScreen from '@/components/ui/LoadingScreen';

// PERF: Dynamically import below-fold sections to avoid ScrollTrigger layout thrashing during LCP
// These sections create ScrollTriggers that cause 600ms+ forced reflows
const About = dynamic(() => import('@/components/sections/AboutSection'), {
  ssr: false,
  loading: () => <section className="min-h-screen" aria-busy="true" />,
});

const Skills = dynamic(() => import('@/components/sections/SkillsSection'), {
  ssr: false,
  loading: () => <section className="min-h-screen" aria-busy="true" />,
});

const Projects = dynamic(() => import('@/components/sections/ProjectsSection'), {
  ssr: false,
  loading: () => <section className="min-h-screen" aria-busy="true" />,
});

const Experience = dynamic(() => import('@/components/sections/ExperienceSection'), {
  ssr: false,
  loading: () => <section className="min-h-screen" aria-busy="true" />,
});

const Contact = dynamic(() => import('@/components/sections/ContactSection'), {
  ssr: false,
  loading: () => <section className="min-h-screen" aria-busy="true" />,
});

const GlobalScene = dynamic(() => import('@/components/three/GlobalScene'), {
  ssr: false,
  loading: () => null,
});

export default function Home() {
  const [isLoading, setIsLoading] = useState(true);
  const [showScene, setShowScene] = useState(false);

  const handleLoadingComplete = useCallback(() => {
    setIsLoading(false);
    // PERF: Defer GlobalScene mount until after LCP to reduce render delay
    // Use requestIdleCallback for non-critical 3D rendering
    if ('requestIdleCallback' in window) {
      requestIdleCallback(() => setShowScene(true), { timeout: 500 });
    } else {
      setTimeout(() => setShowScene(true), 100);
    }
  }, []);

  return (
    <>
      {isLoading && <LoadingScreen onComplete={handleLoadingComplete} />}
      {showScene && <GlobalScene />}
      <Navbar />
      <main className={isLoading ? 'opacity-0' : 'opacity-100 transition-opacity duration-500'}>
        <Hero isLoaded={!isLoading} />
        <About />
        <Skills />
        <Projects />
        <Experience />
        <Contact />
      </main>
      <Footer />
    </>
  );
}
